
.price {
  align-items: flex-start;
  display: flex;
  justify-content: center;
  padding: 64px 0;
  &__more{
    display: flex;
    justify-content: space-around;
    margin-top: 30px;
    h2{
      font-size: 24px;
      font-weight: 600;
    }

  }
  &__left{
    width: 35%;
    text-align: center;
    background: linear-gradient(135deg, rgba(19,146,205,1) 0%, rgba(229,206,3,1) 100%);
    border-radius: 15px;
    color: white;
    padding: 15px 0;

  }
  &__right{
    width: 35%;
    text-align: center;
    background: linear-gradient(135deg, rgba(19,146,205,1) 0%, rgba(229,206,3,1) 100%);
    border-radius: 15px;
    color: white;
    padding: 15px 0;


  }

  &__title {
    color: rgba(19,146,205,1);
    font-size: 44px;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 40px;
    position: relative;
    text-indent: 170px;
    line-height: 70px;

  }
&__imageTitle{
  width: 155px;
  height: 65px;
  position: absolute;
  left: 0;
  top: 3px;
}
  &__imageTitleSky{
    width: 156px;
    height: 41px;
    position: absolute;
    left: 0;
    top: 15px;
  }
  &__cards {
    display: flex;
    align-items: center;
    column-gap: 50px;
  }

  &__card {
    background: linear-gradient(135deg, rgba(19,146,205,1) 0%, rgba(229,206,3,1) 100%);
    color: #FFFFFF;
    text-transform: uppercase;
    padding: 20px 30px;
    border-radius: 20px;
    display: flex;
    align-items: flex-start;
    column-gap: 40px;
    &-title {
      font-size: 40px;
      font-weight: 700;
    }
    &-text {
      text-align: left;
    }

    &-price {
      font-size: 80px;
      font-weight: 900;
      text-align: right;
    }

    &-money {
      font-size: 30px;
      font-weight: 300;
    }

    &-desc {
      font-size: 30px;
      font-weight: 300;

    }
  }
}


