@media (max-width: 769px) {
  .price {
    flex-direction: column;
    align-items: center;
    padding: 40px 0;

  }
  .price-card__txt {
    padding: 10px;
  }

}

@media (max-width: 481px) {
  .price {
    flex-direction: column;
    align-items: center;
    padding: 30px 0;

    .price-card__txt {
      padding: 10px;
    }

    .price-card__img {
      height: 167px;
    }


  }

}